// 此处引入你要的组件
export default {
		data() {
		return {
			//选项卡
			tabList: [{
					text: "全部"
				},
				{
					text: "待审核"
				},
				{
					text: "审核成功"
				},
				{
					text: "审核失败"
				},
			],
			// 选项卡切换状态
			tabActive: 1,
		}
	},
	/**
	 * Vue的组件存放
	 */
	components: {},
	/**
	 * uni-app生命周期
	 * options是上个页面的传参
	 * 指的是页面加载完毕执行的函数
	 */
	onLoad(options) {init();},
	/**
	 * uni-app
	 * 指的是页面获取焦点的时候执行的函数
	 */
	onShow() {},
	/**
	 * uni-app
	 * 指的是页面失去焦点的时候执行的函数
	 */
	onHide() {},
	/**
	 * Vue计算属性
	 * 存放的函数，里面的函数名是自己定义的。函数必须有个返回值
	 */
	computed: {},
	/**
	 * Vue监听器
	 * 存放的函数，里面的函数名通常是data的数据
	 * 监听data的数据，一旦发生改变就执行里面对应的函数
	 */
	watch: {},
	/**
	 * Vue的自定义方法
	 */
	methods: {
		switchTab(index) {
			let that = this
			that.tabActive = index
		
			//模拟切换效果
			let data = new Array()
			console.log(data)
			// return
			let rand = parseInt(Math.random() * that.cardList.length)
			// that.cardList
			that.cardList.forEach((e, index) => {
				if(rand != index){
					data.push(e)
				}
			})
			that.cardList = data
		},
		init() {
			let that = this
			that.tabActive = 0
		},
		backhome(){
			uni.navigateBack(getCurrentPages() - 1)
		}
	}
};
